import React,{useEffect,useState} from 'react'
import {connect} from 'react-redux';
import './style.css';
import Nav from './Nav';
import {useParams,useHistory} from 'react-router-dom';
import io from 'socket.io-client';
import Axios from 'axios';
function Status(props) {
    const [loading, setloading] = useState(true);
    const socket = io('https://afternoon-shelf-38436.herokuapp.com/');
    let ostatus = "";
    const [place, setplace] = useState('');
    const history = useHistory();
    const {id} = useParams();

    socket.on(id,data =>{
        
        if(data.value === 'Confirmed') {
        const othercolor = document.getElementById('Confirmed');
        othercolor.classList.add('step-c');
        
        }
        if(data.value === 'Prepared') {
            const concolor = document.getElementById('Confirmed');
        concolor.classList.add('step-c');
            const othercolor = document.getElementById('Prepared');
            othercolor.classList.add('step-c');
            
        }
        if(data.value === 'Delivered') {
            const concolor = document.getElementById('Confirmed');
            concolor.classList.add('step-c');
                const othercolor = document.getElementById('Prepared');
                othercolor.classList.add('step-c');
            const delcolor = document.getElementById('Delivered');
            delcolor.classList.add('step-c');
            
        }
        if(data.value === 'completed') {
            alert('order completed');
            
        }
        
        
        
    
    });
    
    

    useEffect(() => {
        if(props.isLog === 'false') {
            history.push('/login');
            return null;
        }
        // if(!loading) { 
        // // const changecolor = document.getElementById('Placed');
        // changecolor.classList.add('step-c');
        else{ 
            Axios.get(`https://afternoon-shelf-38436.herokuapp.com/pizza/status/${id}`).then((resp) => {
                const data = Object.assign({},resp.data);

                ostatus = data.sta;
                console.log(ostatus);
                setplace(data.time);
                setloading(false);
                if(!ostatus.localeCompare('Confirmed')) {
                
                  const othercolor = document.getElementById('Confirmed');
                  othercolor.classList.add('step-c');
                  
                  }
                  if(!ostatus.localeCompare('Prepared')) {
                      
                      const concolor = document.getElementById('Confirmed');
                  concolor.classList.add('step-c');
                      const othercolor = document.getElementById('Prepared');
                      othercolor.classList.add('step-c');
                      
                  }
                  if(!ostatus.localeCompare('Delivered')) {
                
                      const concolor = document.getElementById('Confirmed');
                      concolor.classList.add('step-c');
                          const othercolor = document.getElementById('Prepared');
                          othercolor.classList.add('step-c');
                      const delcolor = document.getElementById('Delivered');
                      delcolor.classList.add('step-c');
                      
                  }
              });
        
              
              
        
    }
    
   
    }, [])
    
    
    if(props.isLog === 'false') {
        history.push('/login');
        return null;
    }
    else{ 
        

    return (
     <>
     <Nav />
<section className="status">
    <div className='container mx-auto'>
        <div className="status-box mx-auto w-2/3">
            <div className="flex items-center justify-between mb-12">
                <h1 className="text-xl font-bold">Track your delivery status</h1>
                <h5 className="py-1 px-4 rounded-full bg-white text-green-800">
                {id}
                </h5>
            </div>
            { !loading && <ul>
                <li id="Placed" className="text-xl pb-12 status-line step-c"><span>Order Placed</span> <small>{place} </small> </li>
                <li id="Confirmed" className="text-xl pb-12 status-line"><span>Confirmed</span></li>
                <li id="Prepared" className="text-xl pb-12 status-line"><span>Preparation</span></li>
                <li id="Delivered" className="text-xl pb-12 status-line"><span>out for Delivery</span></li>
                <li id="completed" className="text-xl pb-12 status-line"><span>completed</span></li>
            </ul>}
            {loading && <p className="font-bold text-lg">Loading ...</p>}
        </div>
    </div>
</section>
     </>   
    )
    }
}
const mapStateToProps = (state) => {
    return {
        isLog: state.isLog,
    }
}

export default connect(mapStateToProps)(Status)
