
import {addpizza,Login,Logout,setId,putId,Empty,rpizza} from './actiontypes';



export const add2cart = (id) =>{
    return{
        type: addpizza,
        id
    }
}
export const setLogin = (setL) => {
    return{
        type: Login
        
    }
}
export const delpizza = (id) => {
    return{
        type: rpizza,
        id
        
    }
}
export const setLogout = (setL) => {
    return{
        type: Logout
        
    }
}
export const setcustomer = (id) => {
    return{
        type: setId,
        id
        
    }
}

export const putcustomer = (id) => {
    return{
        type: putId,
        id
        
    }
}
export const Emptycart = () => {
    return{
        type: Empty,
        
        
    }
}




