import Axios from 'axios';
import React, { useEffect,useState } from 'react';
import {connect} from 'react-redux';
import logo from './imgs/logo.png';
import {useHistory,Link} from 'react-router-dom';
import './style.css';
import { setLogout} from './store/actioncreator';
import io from 'socket.io-client';
function Admin(props) {
    const socket = io('https://afternoon-shelf-38436.herokuapp.com/');
    const history = useHistory();
    const [orders, setorders] = useState([]);
    const handleLogout = () => {
        
        props.setLogout('false');
        history.push('/login');
            }
    const handleStatus = (e,id) => {
        
        socket.emit(`com`,{orderid: id,cid: props.customerId});
     socket.emit('changestatus',{value: e.target.value,statusid: id});
    if(e.target.value === 'completed') {
        setorders((curr) => {
            return curr.filter((item) => item._id !== id)
        })
    }
        Axios.post(`https://afternoon-shelf-38436.herokuapp.com/order/status/${id}`,{id: e.target.value}).then(res => {
        
        })

    }
    socket.on('updateorder',data => {
        setorders(orders);
    Axios.get('https://afternoon-shelf-38436.herokuapp.com/admin').then((getorder) => {
        setorders(getorder.data.orders);
            }).catch(() => {});
    })
    useEffect(() => {
    Axios.get('https://afternoon-shelf-38436.herokuapp.com/admin').then((getorder) => {
setorders(getorder.data.orders);
    }).catch(() => setorders([]));
    

    }, [])
    const displayItems = (arr) => {
        return arr.map((p) => {
            return <p> {`${p.name} - ${p.qty} Pcs`} </p>
        })
    }
    const display = () => {
        if(orders.length === 0) {
            return(
                <>
                <tr>
                    <td> Loading... please wait a moment !</td>
                </tr>
                </>
            )
        }
        else{
            return orders.map((Item,index) => {
                let date = new Date(Item.createdAt).toLocaleTimeString();
                return(
                    <>
                    <tr className="mrb">
                        <td>
                            <p>{`${index+1}]  ${Item._id}`}</p>
                            <div>{displayItems(Item.items)}</div>
                        </td>
                        
                        <td>
                            {Item.customerId.name}
                        </td>
                        
                        <td>
                            {Item.address}
                        </td>
                        
                        <td>
                        <div className="inline-block relative w-64">
  <select id={Item._id} onChange={(e) => {handleStatus(e,Item._id)}} className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
    <option value="Placed" selected={Item.status === 'Placed'}>Placed</option>
    <option value="Confirmed" selected={Item.status === 'Confirmed'}> Confirmed</option>
    <option value="Prepared" selected={Item.status === 'Prepared'}>Prepared</option>
    <option value="Delivered" selected={Item.status === 'Delivered'}>Delivered</option>
    <option value="completed" selected={Item.status === 'completed'}>completed </option>
  </select>
  <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
    <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
  </div>
</div>
                        </td>
                        
                        <td>
                            {date}
                        </td>
                    </tr>
                    </>
                )
            })
        }
    }
    if(props.isLog === 'false') {
        history.push('/login');
    }
    return (
      <>
      <nav className="container mx-auto flex justify-between py-4" id="MyNav">
           <div>
               <img src={logo} className="logo" alt="logo" />
           </div>
           <div>
           <ul className="flex items-center">
        
           <li style={{padding: '10px',borderRadius: '10px',color: 'white' }} className="ml-6"> <Link onClick={handleLogout}>Logout</Link> </li>
           </ul>
           
               {/* <ul className="flex items-center">
                   <li className="ml-6"><Link to="/">Menu</Link></li>
                   <li className="ml-6"><Link to="/offer">Offers</Link></li>
                   <li className="ml-6"> <Link to="/register">Register</Link>   </li>
                   <li className="ml-6"> <Link to="/login">Login</Link> </li>
                   <li className="ml-6 rounded-full"> <Link to="/cart" className="inline-block px-4 py-2 cartnum" style={{color: "white"}}>
                   {props.totalqty > 0 ? props.totalqty : null}
                   <img src={cartlogo} alt="cart" />
                   </Link>   </li>
               </ul> */}
           </div>
       </nav>
   
<section className="orders light-section">
        <div className="container mx-auto pt-12">
            <h1 className="font-bold text-lg mb-4">All Orders</h1>
            <table className="table-auto bg-white w-full">
                <thead>
                    <tr>
                        <th className="text-left py-2 px-4">
                             Orders
                        </th>
                        
                        <th className="text-left py-2 px-4">
                             Customer
                        </th>
                        <th className="text-left py-2 px-4">
                             Address
                        </th>
                        
                        <th className="text-left py-2 px-4">
                        Status
                        </th>
                        
                        <th className="text-left py-2 px-4">
                        Placed at
                        </th>
                    </tr>
                </thead>
                <tbody>
{display()}
                </tbody>
            </table>
        </div>
    
    
    </section>
      </>
    )
}
const mapStateToProps = (state) => {
    return {
        isLog: state.isLog,
        customerId: state.customerId
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        setLogout: (bool) => {
            dispatch(setLogout(bool))
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Admin)
