import React from 'react'
import Nav from './Nav';
import heropizza from './imgs/hero-pizza.png';
import {connect} from 'react-redux';
import {add2cart} from './store/actioncreator';
import Noty from 'noty';
import '../node_modules/noty/src/noty.scss';
import '../node_modules/noty/src/themes/mint.scss';
import './not.scss';
import './style.css';
function Home(props) {
  
    return (
        <>
<Nav />
<section className="hero py-16">
        <div className="container mx-auto flex items-center justify-between">
            <div className="w-1/2">
<h4 className="text-lg"> Are You Hungry?</h4>
<h1 className="font-bold text-3xl md:text-6xl"> Don't Wait!</h1>
<button className="font-bold px-6 py-2 mt-4 rounded-full text-white btn">Order Now</button>
            </div>
            <div className="w-1/2">
<img src={heropizza} alt="pizza" />
            </div>
        </div>
    </section>

    <section className="menu container mx-auto py-8">
        <h1 className="text-xl mb-8 font-bold">All Pizzas</h1>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
      {
         props.items.map((Item) => {
          
       return(  
           <>
        <div className="w-full md:w-64 mt-4">
                <img className="h-40 mx-auto mb-4 rnd" src={Item.image} alt="pizza" />
          
            <div className="text-center">
                <h2 className="mb-4 text-lg"> {Item.name}</h2>
                <span className="py-1 px-4 rounded-full uppercase">{Item.size} </span>
            </div>
            <div className="flex items-center justify-around mt-6">
                <span className="font-bold text-lg"> {Item.price} </span>
                <button className="addcart py-1 px-4 rounded-full flex items-center" onClick={() => {props.add2cart(Item.id)
                new Noty({
        text: '1 Item added !',
        progressBar: false,
        timeout: 1000,
        type: "success"
    }).show()}}>
                    <span>+
                    </span>
                    <span className="ml-4">Add</span>
                </button>
                
            </div>
        </div>
</>
       )
         })
      }     


           

    </div>
    </section>


        </>
    )
}
const mapStateToProps = (state) => {
    return {
        items: state.items
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        add2cart: (id) => {
            dispatch(add2cart(id))
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Home);
