
import React,{useState} from 'react'
import { Link } from 'react-router-dom'
import Nav from './Nav';
import { useHistory } from 'react-router-dom';
import Axios from 'axios';

import Noty from 'noty';
import '../node_modules/noty/src/noty.scss';
import '../node_modules/noty/src/themes/mint.scss';
import './not.scss';
function Register() {
  const [loading, setloading] = useState(false);
  const history = useHistory();
  const onsubmit = async (event) => {
   
event.preventDefault();
if(!event.target.name.value || !event.target.email.value || !event.target.pass.value){
  alert('All fields are requird !');
}
else{ 
  setloading(true);
const info = {
  name: event.target.name.value,
  email: event.target.email.value,
  password: event.target.pass.value
}
await Axios.post("https://afternoon-shelf-38436.herokuapp.com/register",info).then(res => {
  setloading(false);
if(res.data.result === '1') {
history.push('/login');
new Noty({
  text: 'Registered successfully!',
  progressBar: false,
  timeout: 2000,
  type: "success"
}).show()
}
else{
  history.push('/register');
  new Noty({
    text: ' server error !',
    progressBar: false,
    timeout: 2000,
    type: 'error'
}).show()
}

})
}
  }
    return (
        <>
<Nav />
<section className="login flex justify-center py-24">
        <div className="w-full max-w-xs">
            <form onSubmit={onsubmit} className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
              <div className="mb-4">
                <label className="block text-gray-700 text-sm font-bold mb-2" for="username">
                  Name
                </label>
                <input name="name" className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter your Name" />
              </div>
              <div className="mb-4">
                <label className="block text-gray-700 text-sm font-bold mb-2" for="username">
                  Email
                </label>
                <input name="email" className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="email" placeholder="Enter your Email" />
              </div>

              <div className="mb-6">
                <label className="block text-gray-700 text-sm font-bold mb-2" for="password">
                  Password
                </label>
                <input name="pass" className="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="******************" />
              
              </div>
              <div className="flex items-center justify-between">
                <button disabled={loading} type="submit" className="btn rounded-full text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">
                  SignUp
                </button>
                <Link className="inline-block align-baseline font-bold text-sm text-blue-500 hover:text-blue-800" to="/login">
                  Already have Accoumt?
                </Link>
              </div>
            </form>
            <p className="text-center text-gray-500 text-xs">
              &copy;2020 Pizza Hut. All rights reserved.
            </p>
          </div>
    
    </section>
        </>
    )
}

export default Register
