import React from 'react'
import './style.css';
import Nav from './Nav';
import cartblack from './imgs/cart-black.png';
import {connect} from 'react-redux';
import Empty from './imgs/empty-cart.png';
import {Link} from 'react-router-dom';
import {Emptycart} from './store/actioncreator';
import Axios from 'axios';
import io from 'socket.io-client';
import Noty from 'noty';
import '../node_modules/noty/src/noty.scss';
import '../node_modules/noty/src/themes/mint.scss';
import './not.scss';
function Cart(props) {
    const socket = io('https://afternoon-shelf-38436.herokuapp.com/')
    
    const handlesub = async (e) => {
        e.preventDefault();
        if(!e.target.phone.value || !e.target.addr.value){
            alert('All fields are required !')
        }
        else{
            
            props.Emptycart();
            
            new Noty({
                text: 'Order Placed!',
                progressBar: false,
                timeout: 1200,
                type: "success"
              }).show()
            const info = {
                phone: e.target.phone.value,
                address: e.target.addr.value,
                items: props.additems,
                customerId: props.customerId

            }
            await Axios.post('https://afternoon-shelf-38436.herokuapp.com/order',info).then(res => {

socket.emit('neworder',"neworder");
            });
            
           
        }
    }
    if(props.totalqty > 0) { 
    return (
     <>
            <Nav />
          
            <section className="cart py-16">
        <div className="order container mx-auto xl:w-1/2">
            <div className="flex items-center border-b border-grey-300 pb-4">
<img src={cartblack} alt="cart" />
<h1 className="font-bold text-2xl ml-4"> Order Summary</h1>
            </div>
            <div className="pizza-list">
                
{
    props.additems.map(Item => {
    
        return(
            <>
            
            <div className="flex items-center my-8">
                <img src={Item.image} className="olist" alt="pizza" />
                <div className="flex-1 ml-4">
                    <h1 className="font-bold text-2xl ml-4"> {Item.name}</h1>
                    <span className="ml-4">{Item.size}</span>
                </div>
                <span className="flex-1">{`${Item.qty} Pcs`} </span>
                <span className="font-bold text-lg">{`â‚¹${Item.price}`} </span>
               
            </div>
            
            </>
        )

    })
}
            </div>
            <hr />

            
        <div className="text-right py-4">
            <div>
                <span className="font-bold text-lg">Total Amount:
                </span>
                <span className="ml-2 font-bold amount text-2xl">{props.total}</span>
            </div>
            <div>
                <form onSubmit={handlesub} className="mt-12 d-col">
                    <input name="phone" type="text" className="border border-grey-400 mb-4 p-2 w-1/2" placeholder="Phone Number" />
                    
                    <input name="addr" type="text" className="border border-grey-400 p-2 w-1/2" placeholder="Your Address" />
                    <div>
                    {
                        props.isLog==="true" && 
                        <button type="submit" className="btn noout text-white font-bold px-5 py-2 mt-6 rounded-full">Order Now</button>
                    }
                    </div>
                </form>
            </div>
          {
            props.isLog==="false" && <Link to="/login" className="btn text-white font-bold px-5 py-2 mt-6 rounded-full inline-block cursor-pointer">Login to Continue</Link>
            
          }
            
        </div>
        </div>

    </section>
     </>
    )
        }
        else{
            return(
                <>
                <Nav />
   <div className="empty-cart py-16">
        <div className="container mx-auto text-center">
            <h1 className="font-bold mb-2 text-3xl">Cart Empty!</h1>
            <p className="font-bold mb-12 text-lg"> You don't have order any Pizza .<br /> To order a Pizza goto Main menu</p>
        <img src={Empty} className="w-2/5 mx-auto" alt="empty cart" />
        <Link to="/" className="inline-block px-6 py-2 text-white btn rounded-full font-bold mt-12"> Go Back </Link>
        </div>
    </div> 
                </>
            )
        }

}
const mapStateToProps = (state) => {
    return {
        additems: state.additems,
        total: state.total,
        totalqty: state.totalqty,
        isLog: state.isLog,
        customerId: state.customerId
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        Emptycart: () => {
            dispatch(Emptycart())
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Cart);
