
import {addpizza,Login,Logout,Empty,setId,putId, rpizza} from './actiontypes';
import p1 from '../imgs/p1.jpg';
import p12 from '../imgs/p12.jpg';
import p2 from '../imgs/p2.jpg';
import p3 from '../imgs/p3.jpg';
import p4 from '../imgs/p4.jpg';
import p5 from '../imgs/p5.jpg';
import p6 from '../imgs/p6.jpg';
import p7 from '../imgs/p7.jpg';
import p8 from '../imgs/p8.jpg';
import p9 from '../imgs/p9.jpg';
import p10 from '../imgs/p10.jpg';
import p11 from '../imgs/p11.jpg';

const initstate = {
    items: [
        {id: "1",qty: 0,name:"Margherita",image: p1,price:"250",size:"samll"},
{id: "2",qty: 0,name:"Double Paneer supreme",image: p2,price:"450",size:"samll"},
        {id: "3",qty: 0,name:"Veg Exotica",image: p3,price:"300",size:"samll"},
        {id: "4",qty: 0,name:"Farmers Pick",image: p4,price:"400",size:"samll"},
        {id: "5",qty: 0,name:"Golden Cheeze",image: p5,price:"475",size:"Medium"},
        {id: "6",qty: 0,name:"Veggie lover",image: p6,price:"500",size:"Medium"},
        {id: "7",qty: 0,name:"Garlik Bread",image: p7,price:"550",size:"Medium"},
        {id: "8",qty: 0,name:"Italian Tikka",image: p8,price:"650",size: 'Medium'}, 
        {id: "9",qty: 0,name:"Spiced Paneer",image: p9,price:"750",size:"Large"},
        {id: "10",qty: 0,name:"Tangy Tomato",image: p10,price:"750",size:"Large"},
        {id: "11",qty: 0,name:"Spiced Italian",image: p11,price:"850",size:"Large"},
        {id: "12",qty: 0,name:"Green Spicy",image: p12,price:"770",size:"Large"}
    ],
    additems: [],
    total: 0,
    totalqty: 0,
    isLog: "false",
    customerId: "0"
};
export const cartReducer = (state = initstate,action) => {

    if(action.type === addpizza) {
        let addItem = state.items.find(Item => Item.id === action.id);
        let exist = state.additems.find(Item => Item.id === action.id);
if(exist) {
    addItem.qty += 1;
    return{
        ...state,
        total: state.total + Number(addItem.price),
        totalqty: state.totalqty + 1
    }
}
else{
    addItem.qty = 1;
    let newtotal = state.total + Number(addItem.price);
    return{
        ...state,
        additems: [...state.additems,addItem],
        total: newtotal,
        totalqty: state.totalqty + 1

    }
}

    }
    else if(action.type === Login) {
        return{
            ...state,
            isLog: "true"
        }
    }
    else if(action.type === rpizza){
        let ritem = state.additems.find((item) => item.id === action.id);
        alert(ritem.id);
        let newtot = state.total - (Number(ritem.qty)*Number(ritem.price));
        let newqty = state.totalqty - Number(ritem.qty);
        let newitems = state.additems.filter((item) => item.id !== action.id)

        return{
            ...state,
            total: newtot,
            additems: newitems,
            totalqty: newqty


        }
    }
    else if(action.type === setId){
        return{
            ...state,
            customerId: action.id

        }
    }

    else if(action.type === Empty){
        return{
            ...state,
            total: 0,
            additems: [],
            totalqty: 0


        }
    }
    
    else if(action.type === putId){
        return{
            ...state,
            customerId: "0"

        }
    }
    else if(action.type === Logout) {
        return{
            ...state,
            isLog: "false",
         
        }
    }
    else{
        return state;
    }
}






