import React,{useState,useEffect} from 'react';
import Axios from 'axios';
import {connect} from 'react-redux';
import Nav from './Nav';
import { Link,Redirect } from 'react-router-dom';
import './style.css';
function Order(props) {
const [ordersdata, setordersdata] = useState([]);

useEffect(() => {
    Axios.get(`https://afternoon-shelf-38436.herokuapp.com/order/${props.customerId}`).then(res => {
        setordersdata(res.data.orders);
       
         
    }).catch(() => {
        setordersdata([]);
    })
}, [])
const displayOrder = () => {
    if(ordersdata.length === 0) {
        return(
            <>
            <tr>
                <td>
                    Orders not Found !
                </td>
            </tr>
            </>
        )
    }
    return ordersdata.map((Item) => {
        let date = new Date(Item.createdAt).toLocaleTimeString();
        let links = `/status/${Item._id}`;
        return(
            <>
         <tr className="mrb">  
    <td>
        <Link to={links} className="link text-green-800">{Item._id}</Link>
    </td>
    
    <td>
    {Item.address}
    </td>
    
    <td>
    {date}
    </td>
    </tr> 
            </>
        )
    })
}
if(props.isLog === 'false'){
    return(
        <Redirect to="/login" />
    )
}
else{
    return (
        <>
        <Nav />
        <section className="orders light-section">
        <div className="container mx-auto pt-12">
            <h1 className="font-bold text-lg mb-4">All Orders</h1>
            <table className="table-auto bg-white w-full md:mx-auto">
                <thead>
                    <tr>
                        <th className="text-left py-2 px-4">
                             Orders
                        </th>
                        
                        <th className="text-left py-2 px-4">
                             Address
                        </th>
                        
                        <th className="text-left py-2 px-4">
                        Time
                        </th>
                    </tr>
                </thead>
                <tbody>
                {displayOrder()}
                </tbody>
            </table>
        </div>
    
    
    </section>
        </>
     )
}
}
const mapStateToProps = (state) => {
    return {
        customerId: state.customerId,
        isLog: state.isLog
    }
}
export default connect(mapStateToProps)(Order)